.back-to-top-component {
	position: fixed;
	width: 40px;
	height: 40px;
	background-color: #000;
	border-radius: 100%;
	border: 4px solid #fff;
	z-index: 254;
	bottom: 20px;
	right: 20px;
	font-size: 0px;
	cursor: pointer;
	transition: .3s;

	&.active {
		bottom: 100px;
		opacity: 0;
		cursor: none;
	}
	
	span {
		position: absolute;
		display: inline-block;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		font-size: 24px;
		line-height: .9;
		color: #fff;
	}
	
}
