* {
	box-sizing: border-box;
	-webkit-tap-highlight-color: transparent;
}

::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}
 
::-webkit-scrollbar-track {
	background-color: #eee;
}
 
::-webkit-scrollbar-thumb {
  background-color: #888;
}

*::selection {
	background-color: #000;
	color: #fff;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: Arial;
}

body {
	margin: 0;
	overflow-x: hidden; 
	font-family: arial;
	color: #333;
  background-color: #e1e1e1;
}

.container {
	max-width: 1200px;
	margin: auto;
}

.cursor-pointer {
	cursor: pointer;
}

.full-width {
	width: 100%;
}

figure {
	margin: 0;
	font-size: 0;
}

figure > * {
	font-size: 16px;
}

img {
	width: 100%;
}

ul {
	list-style: none;
	padding: 0;
	margin: 0;
}

a {
	text-decoration: none;
	color: #76de5b;
}

h1,h2,h3,h4,h5,h6,p {
	margin: 0;
}

.ibc {
	font-size: 0;

	> * {
		font-size: 16px;
		display: inline-block;
		vertical-align: top;
	}
}

.ibc.ibc-m {
	> * {
		vertical-align: middle;
	}
}

.ibc.ibc-b {
	> * {
		vertical-align: bottom;
	}
}

input,
textarea,
button,
select {
	font-size: 14px;
	&:focus {
		outline: none;
	}
	
}

button {
	transition: .3s;
	cursor: pointer;
	vertical-align: middle;
}

input {
	transition: .3s;
	vertical-align: middle;
}

.default-btn {
	padding: 8px 25px;
	border-radius: 3px;
	border: 0px solid;
}

.green-btn {
	background-color: #76de5b;
	color: #fff;
}

.green-btn:hover {
	background-color: #fff;
	color: #76de5b;
}

.orange-btn {
	background-color: #ef9700;
	color: #fff;
}

.orange-btn:hover {
	background-color: #fff;
	color: #ef9700;
}

.red-btn {
	background-color: #e45757;
	color: #fff;
}

.red-btn:hover {
	background-color: #fff;
	color: #e45757;
}

.default-input {
	padding: 10px 25px;
	border-radius: 3px;
	border: 1px solid #7cc7f6;
	width: 250px;
	color: #333;
	background-color: #fff;
}

.default-input[type=checkbox] {
	width: auto;
}

.default-input:focus {
	border: 1px solid #7cc7f6;
	background-color: #e4f4f9;
}

textarea {
	resize: none;
}

table {
	border-collapse: collapse;
}

@for $i from 1 through 12 {
	.col-s#{$i},
	.col-m#{$i},
	.col-l#{$i} {
		width: 8.3333333% * $i;
		padding: 0 15px;
	}
}

.page-change-enter {
  opacity: 0;
}

.page-change-enter.page-change-enter-active {
  opacity: 1;
  transition: opacity 300ms ease-in;
}

.page-change-leave {
  opacity: 1;
}

.page-change-leave.page-change-leave-active {
  opacity: 0;
  transition: opacity 300ms ease-in;
}

.page-change-appear {
  opacity: 0;
}

.page-change-appear.page-change-appear-active {
  opacity: 1;
  transition: opacity .5s ease-in;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

@keyframes jump_out {
    0% {
    	opacity: 0;
    	margin-top: 0;
    }

    25% {
			margin-top: -20px;
    }

    50% {
			opacity: 1;
			margin-top: 0;
    }

    75% {
			margin-top: -10px;
    }

    100% {
			opacity: 1;
    	margin-top: 0;
    }
}

@media only screen and (max-width: 767px) {
    .page-contact-us {
        h1 {
            font-size: 30px;
        }
    }
}

@media only screen and (max-width: 1199px) {
	@for $i from 1 through 12 {
		.col-l#{$i} {
			width: 100%;
		}
	}

	.container {
		max-width: 960px;
	}
}

@media only screen and (max-width: 991px) {
	@for $i from 1 through 12 {
		.col-m#{$i} {
			width: 100%;
		}
	}
}

@media only screen and (max-width: 767px) {
	@for $i from 1 through 12 {
		.col-s#{$i} {
			width: 100%;
		}
	}
}