$dizzy-size: 400px;

.not-found-404 {
	padding: 50px 15px;
	font-size: 100px;
	text-align: center;
	position: relative;
	min-height: 450px;

	p {
		font-size: 20px;
		color: #333;
	}
	
	p.title {
		font-size: 90px;
		color: #e45757;

		span {
			img {
				width: 50px;
			}
		}
	}

	p.sub-title {
		font-size: 30px;
		margin-top: 15px;
		margin-bottom: 15px;
		font-weight: bold;
	}

	.dizzy {
		position: absolute;
		right: 80%;
		bottom: 50%;
		transform: translate(50%, 50%);
		opacity: .1;
		font-size: $dizzy-size;
		height: $dizzy-size;
		z-index: -1;
		color: #3c5caa;
	}

	a {
		color: inherit;
	}

	button {
		margin: 0 15px;
	}
}

@media only screen and (max-width: 767px) {
	$dizzy-size: 300px;

	.not-found-404 {
		padding: 30px 15px;
		font-size: 60px;
		min-height: 350px;

		p {
			font-size: 16px;
		}

		.dizzy {
			font-size: $dizzy-size;
			height: $dizzy-size;

		}
	}
}

@media only screen and (max-width: 424px) {
	$dizzy-size: 200px;

	.not-found-404 {
		padding: 30px 15px;
		font-size: 40px;

		p {
			font-size: 14px;
		}

		.dizzy {
			font-size: $dizzy-size;
			height: $dizzy-size;
		}

		button {
			margin: 5px;
		}
	}
}