.loadingfullpage-component {
	.loader {
		z-index: 255;
		width: 100%;
		height: 100vh;
		left: 0;
		top: 0;
		background-color: #000c;
		position: fixed;

		.loader-wrapper {
			position: absolute;
			top: 50%;
			left: 50%;
			width: 80px;
			height: 80px;
			margin-top: -40px;
			margin-right: -40px;
			border-radius: 50%;
			border: 8px solid #7CC7F6;
			border-left: 8px solid transparent;
			box-sizing: border-box;
      animation: spin 1s linear infinite;
		}
	}
}