.loadingcircle-component {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	z-index: 255;
	background-color: #fffa;
	.loader {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		.loader-wrapper {
			width: 80px;
			height: 80px;
			border-radius: 50%;
			border: 8px solid #7CC7F6;
			border-left: 8px solid transparent;
			box-sizing: border-box;
      animation: spin 1s linear infinite;
		}
	}
}