div.home-page {
	.images-wrapper {
		padding-bottom: 100px;
		.image-header {
			padding: 15px 0;
			border-bottom: 2px solid #004445;
			position: fixed;
			width: 100%;
			background-color: #e1e1e1;

			ul {
				text-align: center;
				li {
					width: 100px;
					border: 2px solid #00444500;

					&.active {
						border: 2px solid #004445;
					}
					figure {
						img {

						}
					}
				}
			}
		}

		.image-header-clone {
			height: 132px;

		}

		.image-list {
			padding: 30px 0;
			ul {
				text-align: center;
				li {
					width: 280px;
					margin-left: 15px;
					margin-right: 15px;
					margin-bottom: 45px;

					.li-wrapper {
						figure {
							img {

							}
						}
					}

					.color-wrapper {
						margin-top: 15px;
						div {
							span {
								height: 30px;
								width: 30px;
								display: inline-block;
							}
						}
					}

					.code {
						margin-top: 15px;
					}
				}
			}
		}

		.pagination {
			ul {
				text-align: center;
				li {
					margin-left: 15px;
					margin-right: 15px;
					font-size: 20px;
					cursor: pointer;

					span.active {
						font-weight: bold;
					}
				}
			}
		}
	}
}

